import mInput from '../../components/m-input.vue';
export default {
    components: {
        mInput			
    },
    data() {
        return {
            num: '',
            vercode: '',
			countdown:60
        }
    },
	onLoad(options){
		this.num=options.num;
	},
    methods: {
		numberst(e){
					//其他代码....
					this.countDown();
				},
				// 倒计时
				countDown(){
					let self = this;
					self.countdown = 60;
					self.countdown -= 1;
					if(self.clear){
						clearInterval(self.clear)
					}
					self.clear = null;
					self.clear = setInterval(_ => {
						if(self.countdown > 0){
							self.countdown -= 1;
						}else{
							clearInterval(self.clear)
						}
					},1000)
				},	
        vertifynum() {        
            uni.navigateTo({
                url:"/pages/userinfo/changephone"
            });
        }
    }
}
